.servcont {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.usCont,
.mixCont,
.aloneCont {
  color: whitesmoke;
}

.mixCont {
  flex-direction: row-reverse;
}

.aloneCont {
  flex-direction: row;
}

.ContInner {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 65%;
  min-width: 50%;
  flex-grow: 1;
  text-align: left;
  font-size: 1rem;
  border: 1px solid whitesmoke;
  height: 100%;
  padding: 1rem;
  margin: 1rem;
}

.imgExpedition {
  height: auto;
  max-width: 100%;
  margin: 1rem;
}

.usCont,
.aloneCont,
.mixCont {
  margin: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 25%;
  width: 98%;
}

@media only screen and (max-width: 983px) {
  .imgExpedition {
    display: none;
  }
  .usContInner {
    width: 100%;
  }

  .ContInner {
    flex-shrink: 0;
    flex-grow: 0;
    margin: 0;
    width: 90%;
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 321px) {
  .ContInner {
    font-size: 0.7rem;
  }
}
