.footer {
  position: absolute;
  height: 3%;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.logo {
  color: whitesmoke;
}

.footBar {
  display: flex;
  justify-content: space-around;
  text-align: center;
  flex-basis: 40%;
}

.linkF {
  font-size: 0.8rem;
  color: whitesmoke;
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  .footBar {
    flex-basis: 60%;
  }
}
