.usCon {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.missionCont {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  flex-basis: 20%;
  text-align: center;
  border: 1px solid whitesmoke;
  color: whitesmoke;
  min-height: 250px;
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 1rem;
}

.missionCont:hover {
  background-color: whitesmoke;
  color: rgb(234, 157, 34);
  transition: 1s;
  border: 4px solid rgb(255, 157, 0);
  position: relative;
  bottom: 5px;
}

@media only screen and (max-width: 768px) {
  .usCon {
    flex-wrap: wrap;
    align-content: center;
  }
  .missionCont {
    min-height: 150px;
    flex-basis: 70%;
  }
}

@media only screen and (max-width: 320px) {
  .missionCont {
    min-height: 110px;
    flex-basis: 90%;
  }
}
