* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body,
.App,
.root {
  height: 100vh;
  background: rgb(234, 157, 34);
  background: linear-gradient(
    38deg,
    rgba(234, 157, 34, 1) 0%,
    rgba(234, 138, 86, 1) 48%,
    rgba(239, 71, 6, 1) 100%,
    rgba(189, 206, 195, 1) 100%,
    rgba(0, 255, 85, 1) 100%,
    rgba(14, 145, 255, 1) 100%,
    rgba(0, 255, 25, 1) 100%,
    rgba(18, 159, 255, 1) 100%,
    rgba(50, 116, 255, 1) 100%,
    rgba(222, 222, 222, 1) 100%
  );
}

.App {
  font-family: "Righteous", cursive, "serif";
  font-size: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
