.home {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.titleCont h1,
.titleCont p {
  color: whitesmoke;
}

.titleCont h1 {
  font-size: 3rem;
}

.titleCont p {
  font-size: 1.3rem;
}

.linkHome {
  text-decoration: none;
  color: whitesmoke;
  font-size: 1.2rem;
  border: 1px solid whitesmoke;
  padding: 0.3rem 0.5rem;
  border-radius: 20rem;
}

.linkHome:hover {
  color: rgba(94, 90, 90, 0.726);
  background-color: whitesmoke;
  transition: 1s;
}

.imgCont img {
  max-width: 100%;
  height: 300px;
  border-radius: 2rem;
}

@media only screen and (max-width: 923px) {
  .home {
    flex-wrap: wrap;
    height: 70%;
  }
}

@media only screen and (max-width: 475px) {
  .home {
    height: 50%;
  }
  .titleCont h1 {
    font-size: 1.7rem;
  }

  .titleCont p {
    font-size: 0.8rem;
  }

  .linkHome {
    font-size: 0.8rem;
    padding: 0.3rem;
  }

  .imgCont img {
    height: 200px;
  }
}

@media only screen and (max-width: 321px) {
  .home {
    height: 70%;
  }
}
