.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  color: whitesmoke;
  flex-wrap: wrap;
  height: 2.5rem;
}

.header h1 {
  flex-basis: 20%;
  text-align: center;
  flex-grow: 1;
  font-size: 1rem;
}

.navBar {
  flex-grow: 1;
  flex-basis: 40%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  list-style: none;
  flex-basis: 20%;
}

.link {
  border-radius: 2rem;
  font-size: 1rem;
  text-decoration: none;
  padding: 0.2rem 0.4rem;
  flex-basis: auto;
}

/* unvisited link */
.link:link {
  color: whitesmoke;
}

/* visited link */
.link:visited {
  color: whitesmoke;
}

/* mouse over link */
.link:hover {
  background-color: #d4d4d4;
  transition: 1s;
}

/* selected link */
.link:active {
  color: rgb(234, 157, 34);
}

@media only screen and (max-width: 768px) {
  .header h1 {
    flex-basis: 100%;
    text-align: center;
  }
  .navBar {
    flex-basis: 100%;
  }

  .link {
    text-align: center;
  }
}
